#projects {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#projects-title {
    text-align: center;
    margin-top: 50px;
}

#projects-title > p {
    font-size: large;
}

#projects-list {
    margin-top: 20px;
    padding: 10px;
    width: 50%;
    max-width: 1200px;
    border-radius: 10px;
    background-color: var(--color-light);
    backdrop-filter: blur(1px);
    transition: ease-in .2s;
}

#projects-list:hover {
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(2px);
}

#projects-list > ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    gap: 15px;
}

#projects-list > ul > li {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#projects-list > ul > li > img {
    aspect-ratio: 16 / 9;
    width: 90%;
    border-radius: 7px;
}

.linkProjectButton {
    display: block;
    width: 90%;
    color: #000000;
    text-align: center;
    font-size: small;
    font-weight: normal;
    border-radius: 7px;
    padding: 10px;
    margin-top: 10px;
    background-color: var(--color-light);
    backdrop-filter: blur(0.5px);
    transition: ease-in-out .3s;
}

.linkProjectButton:hover, .linkProjectButton:active, .linkProjectButton:focus {
    background-color: rgba(83, 226, 236, 0.425);
}

@media screen and (max-width: 850px) {
    #projects-list {
        width: 60%;
    }
}

@media screen and (max-width: 720px) {
    #projects-list {
        width: 70%;
    }
}

@media screen and (max-width: 500px) {
    #projects-list {
        width: 80%;
    }
}

@media screen and (max-width: 400px) {
    #projects-list {
        border-radius: 0;
        width: 100%;
    }

    #projects-title > p {
        font-size: medium;
    }
}